<template>
    <div class="container">
        <v-back title="宿管综合" />
        <md-field>
            <div class="title flex space-between align_center">
                <div class="flex align_center" style="margin-right: 1vw;">【{{typeArr[$route.query.type]}}】{{$route.query.total}}{{$route.query.type == '1'?'条记录':'人'}}</div>
                <div class="flex align_center" style="width: 30vw;justify-content: flex-end;">{{$route.query.time}}</div>
            </div>
            <md-field-item title="选择院系" :content="selectorValue3" @click="showSelector3" arrow solid />
        </md-field>
        <md-selector v-model="isSelectorShow3" :data="data3" @confirm="onSelectorConfirm3" min-height="320px" max-height="320px" title="选择院系" okText="确认" large-radius></md-selector>
        <div class="table">
            <div class="table_body">
				<div style="flex-wrap: wrap;" class="flex space-between" v-if="$route.query.type != '1'">
				    <div v-for="(item, index) in list" :key="index" class="banner">
						<div @click="fullShow(item)">
							<span class="usertype_title usertype_S"></span>
							<div>姓名:&nbsp;&nbsp;<span class="username">{{item.username || '--'}}</span></div>
							<div style="flex-wrap: wrap;display: flex;">学号:&nbsp;&nbsp;<span>{{item.usernbr}}</span></div>
							<div>班级:&nbsp;&nbsp;<span>{{item.college_name  || '--'}}{{item.org_names}}{{item.class_names}}</span></div>
							<div>住宿:&nbsp;&nbsp;<span>{{item.building_name}}{{item.dorm_nbr}}间{{item.bed_nbr}}床</span></div>
							<div style="padding: 2vw 0;">当时通行：<span style="font-size: 3.6vw;font-weight: bolder;" :style="item.entry_exit_type == '1' ? 'color: #1acfc6;' : item.entry_exit_type == '2' ? 'color: red;' : 'color: #ccc;'">({{item.entry_exit_type == '1' ? '进' : item.entry_exit_type == '2' ? '出' : '未知'}})</span><span>{{item.snap_camera_name}}</span><span> {{item.entry_exit_time}}</span></div>
							<div>当时状态:&nbsp;&nbsp;
								<span style="color: #2f86f6;">{{ personStatusText(item) }} | </span>
								<span :class="statusText(item) == '在寝' ? 'in_status' : statusText(item) == '未归' ? 'out_status' : 'undiefind_status'">{{statusText(item)}}</span>
							</div>
						</div>
				    </div>
				</div>
				
				<div v-for="(item, index) in list" :key="index" class="list" @click="fullShow(item)" v-if="$route.query.type == '1'">
					<div class="flex">
						<img v-if="item.photo || item.snap_photo" :src="item.snap_photo || item.photo" alt="" @click.stop="imgsShow(item.snap_photo || item.photo)">
						<img v-else src="@/assets/student.png" alt="">
						<div>
							<div class="flex">
								<h4>{{item.student_name}}</h4>({{usertypeText(item.usertype)}}) {{item.student_nbr}}&nbsp;
								<span v-if="item.usertype == 'S'" :class="statusText(item) == '在寝' ? 'active' : (statusText(item) == '未归' ? '' : 'noactive')">[{{statusText(item)}}]</span>
							</div>
							
							<div style="padding: 2vw 0;">通行通道：<span style="font-size: 3.6vw;font-weight: bolder;" :style="item.entry_exit_type == '1' ? 'color: #1acfc6;' : item.entry_exit_type == '2' ? 'color: red;' : 'color: #ccc;'">({{item.entry_exit_type == '1' ? '进' : item.entry_exit_type == '2' ? '出' : '未知'}})</span>{{item.snap_camera_name}}</div>
							<div>通行时间：{{item.entry_exit_time}}</div>
						</div>
					</div>
				</div>
				
                <div v-show="list.length == 0" class="no_data">
                    <md-result-page text="没内容？切换院系试一试！"></md-result-page>
                </div>
            </div>
        </div>
        <md-landscape v-model="showFullScreen" :full-screen="true">
            <div style="text-align: left;padding: 4vw;font-size: 4vw;line-height: 6vw;">
                <div style="margin-bottom: 4vw;">详细信息</div>
                <div class="flex align_center" style="background-color: #1acfc6;box-sizing: border-box;padding: 2vw;border-radius: 1vw;">
                    <img v-if="showData.photo" :src="showData.photo" alt="" style="box-sizing: border-box;width: 30vw;margin: 0 auto;display: block;box-shadow: 2px 2px 5px rgba(0,0,0,.2);border: 1px solid #f5f5f5;"  @click="imgsShow(showData.photo)">
                    <img v-else src="@/assets/student.png" alt="" style="padding-top: 1vw;background-color: #fff;box-sizing: border-box;width: 30vw;margin: 0 auto;display: block;box-shadow: 2px 2px 5px rgba(0,0,0,.2);border: 1px solid #f5f5f5;">
        			<div class="flex btns">
        			    <div>
        			        <div @click="statusChange(1)">在寝</div>
        			    </div>
        			    <div>
        			        <div @click="statusChange(0)">未归</div>
        			    </div>
        			    <div>
        			        <div @click="statusChange(2)">实习</div>
        			    </div>
        			    <div>
        			        <div @click="statusChange(3)">请假</div>
        			    </div>
        				<!-- <div>
        				    <div @click="statusChange">毕业</div>
        				</div> -->
        			</div>
        		</div>
        		<div class="person_list_text">
        			<div class="flex">
        				当时状态：
        				<div class="full_status">
        					{{ personStatusText(showData) }} | 
        					<span :class="statusText(showData) == '在寝' ? 'active' : (statusText(showData) == '未归' ? '' : 'noactive')">{{statusText(showData)}}</span>
        				</div>
        			</div>
        			<div class="flex align_center" v-show="personStatusText(showData) != '正常'">请假/实习：{{aroundTime}} <md-icon name="id-card" size="lg" @click="imgsShow(showData.person_io_leave_practices[0].photo)"></md-icon></div>
        			<div class="flex">抓拍图片：<img v-show="showData.snap_photo" style="width: 15vw;height: 15vw;" @click="imgsShow(showData.snap_photo)" :src="showData.snap_photo" alt=""></div>
        			<div>学生姓名：{{showData.student_name}}</div>
        			<div>学生学号：{{showData.student_nbr}}</div>
        			<div>学院名称：{{showData.org_names}}</div>
        			<div>班级名称：{{showData.class_names || '--'}}</div>
        			<div>住宿信息：{{showData.building_name}}{{showData.dorm_nbr || '--'}}间{{showData.sys_user ? showData.sys_user.bed_nbr : (showData.bed_nbr || '--')}}床</div>
        			<div>当时通行：<span style="font-size: 3.6vw;font-weight: bolder;" :style="showData.entry_exit_type == '1' ? 'color: #1acfc6;' : showData.entry_exit_type == '2' ? 'color: red;' : 'color: #ccc;'">({{showData.entry_exit_type == '1' ? '进' : showData.entry_exit_type == '2' ? '出' : '未知'}})</span>{{showData.snap_camera_name}} {{showData.entry_exit_time}}</div>
        			<div>备注：{{showData.modify_in_dorm_status_logs && showData.modify_in_dorm_status_logs.length > 0 ? '在寝状态由【' + showData.modify_in_dorm_status_logs[0].modify_user_name + '】在【' + showData.modify_in_dorm_status_logs[0].modify_time +'】修正过' : '--'}}</div>
					<md-button type="primary" size="small" round @click="toRecord2(showData)" style="margin-top: 4vw;">通行记录</md-button>
				</div>
            </div>
        </md-landscape>
		<!-- 图片预览 -->
		<md-image-viewer
			v-model="isViewerShow"
			:list="imgs"
			:has-dots="true">
		</md-image-viewer>
    </div>
</template>

<script>
	var dayjs = require('dayjs')
    export default {
        name: 'report2_list',
        data() {
            return {
                typeArr: ['学生总数量','全部通行记录','24-72小时未归寝人员','72小时以上未归寝人员','24小时以上未出寝人员','当日未归人员','闭寝后晚归人员','闭寝后早出人员','请假实习'],
                list: [],
                selectorValue3: '',
                value3: '',
                isSelectorShow3: false,
                data3: [],
                showFullScreen: false,
                showData: {

                },
				aroundTime: '',
				isViewerShow: false,
				imgs: [],
				data: [
					{
						value: 'S',
						text: '学生'
					},
					{
						value: 'T',
						text: '教师'
					},
					{
						value: 'V',
						text: '访客'
					},
					{
						value: 'Y',
						text: '黄名单'
					},
					{
						value: 'I',
						text: '陌生人'
					}
				],
            }
        },
        methods: {
			usertypeText(type) {
				let usertypename = ''
				this.data.forEach(item => {
					if (item.value == type) {
						usertypename = item.text
					}
				})
				return usertypename
			},
			personStatusText(item) {
				let text = '无记录'
				if (item.entry_exit_status != null) {
					if (item.entry_exit_status == '0') {
						text = '正常'
					}
				}
				if (item.person_io_leave_practices && item.person_io_leave_practices.length > 0) {
					this.aroundTime = dayjs(item.person_io_leave_practices[0].no_attend_starttime).format('YYYY-MM-DD') + ' ~ ' + dayjs(item.person_io_leave_practices[0].no_attend_endtime).format('YYYY-MM-DD')
					if (item.person_io_leave_practices[0].type == 1) {
						text = '请假'
					} else {
						text = '实习'
					}
				}
				return text
			},
			statusText(item) {
				if (item.modify_in_dorm_status_logs && item.modify_in_dorm_status_logs.length > 0) {
					if (item.modify_in_dorm_status_logs[0].in_dorm_status == 0) {
						return '未归'
					} else if (item.modify_in_dorm_status_logs[0].in_dorm_status == 1) {
						return '在寝'
					} else {
						return '未知'
					}
				} else {
					if (item.entry_exit_type == 1) {
						return '在寝'
					} else  if (item.entry_exit_type == 2) {
						return '未归'
					} else {
						return '未知'
					}
				}
			},
			imgsShow(img) {
				this.isViewerShow = true
				this.imgs = [img]
			},
            showSelector3() {
                this.isSelectorShow3 = true
            },
            onSelectorConfirm3({text, value}) {
                this.value3 = value
                this.selectorValue3 = text
                this.getList()  
            },
            //获取学院
            getDepartment() {
                this.$getL('/college/department', {
                    userId: JSON.parse(localStorage.getItem('user')).user_id
                }, (res)=>{
                    this.data3 = [{
                        value: 'all',
                        text: '全部'
                    }]
                    res.data.forEach((item) => {
                        this.data3.push({
                            value: item.orgId,
                            text: item.orgName
                        })
                    })
                    this.getList()
                })
            },
            getList() {
                this.$getL('/comprehensive/list', {
                    userId: JSON.parse(localStorage.getItem('user')).user_id,
                    type: this.$route.query.type,
                    buildingId: this.$route.query.buildingId,
                    college_id: this.value3,
                    entry_exit_time: this.$route.query.time
                }, (res)=>{
                    console.log(res)
                    this.list = res.data
                    this.$toast.hide()
                })
            },
            fullShow(itm) {
                this.showData = itm
				this.showFullScreen = true
            },
			toRecord2(item) {
				if (this.$route.query.type != '1') {
					this.$router.push('/record/index?usernbr=' + item.usernbr + '&username=' + item.username)
				} else {
					this.$router.push('/record/index?usernbr=' + item.student_nbr + '&username=' + item.student_name)
				}
				
			},
            //改变状态
            statusChange(status) {
				this.$getL('/dorm/last_change', {
				    userId: JSON.parse(localStorage.getItem('user')).user_id,
					username: JSON.parse(localStorage.getItem('user')).username,
				    studentNbr: this.showData.usernbr,
					entry_exit_time: this.showData.entry_exit_time,
				    status: status,
					entry_exit_id: this.showData.entry_exit_id
				}, (res)=>{
				    this.$toast.succeed('修改成功')
					let in_dorm_status = '2'
					if (this.showData.last_person_entry_exit) {
						in_dorm_status = this.showData.last_person_entry_exit.in_dorm_status
					}
					let remark = dayjs().format('YYYY-MM-DD HH:mm:ss') + ' ' + JSON.parse(localStorage.getItem('user')).username + ' 手动将人员在寝状态由' + (in_dorm_status == 0 ? '【未归】' : in_dorm_status == 1 ? '【在寝】' : '【未知】') + '修改为' + (status == 0 ? '【未归】' : '【在寝】')
					
					if (this.showData.last_person_entry_exit) {
						this.showData.last_person_entry_exit.in_dorm_status = status
						this.showData.last_person_entry_exit.remark = remark
						this.showData.last_person_entry_exit.entry_exit_status = 0
					} else {
						this.showData.last_person_entry_exit = {
							in_dorm_status: status,
							entry_exit_status: 0,
							remark: remark
						}
					}
				    setTimeout(() => {
						this.$toast.hide()
					}, 500)
				    
				})
            },
        },
        mounted() {
            this.getDepartment()
            this.selectorValue3 = '全部'
            this.value3 = 'all'
        }
    }
</script>

<style scoped>
	.md-field {
		padding: 2vw 4vw;
	}
    .title {
        border-bottom: 1px solid #e2e4ea;
        padding: 0 0 4vw;
        font-size: 3.5vw;
    }
    .margin_right_space {
        margin-right: 1vw;
    }
    .table {
        font-size: 3.5vw;
    }
    .table_body {
        height: calc(100vh - 42vw);
        overflow-y: auto;
		padding: 4vw;
    }
    .no_data {
        margin-top: 20vw;
    }
	.banner {
		background-color: #fff;
		width: calc(50% - 2vw);
		border-radius: 1vw;
		margin-bottom: 4vw;
		box-shadow: 5px 5px 10px rgba(0,0,0,.1);
		color: rgba(0,0,0,.5);
		position: relative;
		overflow: hidden;
		min-height: 70vw;
	}
	.banner div{
	    padding: 1vw;
	}
	.banner .md-icon {
		position: absolute;
		width: 8vw;
		height: 8vw;
		bottom: 2vw;
		right: 2vw;
		font-size: 7vw;
		box-shadow: 2px 2px 5px rgba(0,0,0,.3);
		border-radius: 50%;
		text-align: center;
		line-height: 8vw;
		z-index: 2;
	}
	.banner .md-icon.active {
		transform: rotateZ(45deg);
	}
	.banner span {
		color: #2c3e50;
		font-weight: bolder;
	}
	.banner span.username {
		font-weight: bolder;
		font-size: 4.6vw;
	}
	.banner_title {
		font-size: 3.5vw;
	}
	.usertype_title {
		display: block;
		border-radius: 1vw 1vw 0 0;
		padding: 1vw;
		margin: -1vw -1vw 0 -1vw;
		background-color: #666;
	}
	.usertype_S {
		background-color: #1acfc6;
	}
	.usertype_T {
		background-color: cornflowerblue;
	}
	.usertype_V {
		background-color: #ccc;
	}
	.usertype_Y {
		background-color: yellow;
	}
	.usertype_U {
		background-color: #666;
	}
	
	.list {
	    padding: 3vw;
	    margin-top: 4vw;
	    border: 1px solid #e2e4ea;
		font-size: 3.8vw;
		background-color: #ffffff;
	}
	.list h4 {
		margin-right: 2vw;
	}
	.list img {
		width: 20vw;
		height: 23vw;
		margin-right: 4vw;
	}
	.list span.active {
	    color: #2f86f6;
	}
	.list span.noactive {
	    color: #ccc;
	}
	.list span {
	    color: #fdb731;
	}
</style>